@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* blog previews / list */
.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

/*blog detail page*/
.blog-details h1{
  color: #f1356d;
  margin-bottom: 10px;
}
.blog-details div{
  margin: 20px 0;
}
.blog-details button{
  background: #f1356d;
  padding: 10px;
  border: 0px;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
}

/* Create blog */
.create{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label{
  text-align: left;
  display: block;
}
.create h2{
  font-size: 20px;
  color: #f1356d;
  text-align: center;
  margin-bottom: 30 px;
}

.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin : 10px 0px;
  box-sizing: border-box;
  display: block;
  border:1px solid #ddd;
}

.create button{
  background: #f1356d;
  padding: 10px;
  border: 0px;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
}

/* Not Found page */
.notfound{
  text-align: center;
}
.notfound h1{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.notfound a{
  position: relative;
  top: 90px;
  color: red;
}